import { useEffect, useRef, useState } from "react";
import {
  hexToHue,
  hslToHex,
  hslToRgb,
  hexToHsl,
} from "../colors.ts";

// Generate random starting colors between 90 and 270 degrees apart
function generateRandomColors(): { low: string; high: string } {
  const hue1 = Math.random();

  // Generate separation between 90 and 270 degrees
  const minSeparation = 90 / 360;
  const maxSeparation = 270 / 360;
  const separation = minSeparation + Math.random() * (maxSeparation - minSeparation);

  // Randomly choose direction
  const direction = Math.random() < 0.5 ? 1 : -1;
  const hue2 = (hue1 + separation * direction + 1) % 1;

  const color1 = hslToRgb(hue1, 1.0, 0.5);
  const color2 = hslToRgb(hue2, 1.0, 0.5);

  const hex1 = `#${color1.r.toString(16).padStart(2, '0')}${color1.g.toString(16).padStart(2, '0')}${color1.b.toString(16).padStart(2, '0')}`;
  const hex2 = `#${color2.r.toString(16).padStart(2, '0')}${color2.g.toString(16).padStart(2, '0')}${color2.b.toString(16).padStart(2, '0')}`;

  return { low: hex1, high: hex2 };
}

// Build heat LUT with stable direction (spectrum mode)
function buildStableHeatLut256(
  lowHex: string,
  highHex: string,
  directionRef: React.MutableRefObject<number>,
  useSpectrum: boolean
): Uint32Array {
  const lut = new Uint32Array(256);

  if (!useSpectrum) {
    // Gradient mode: simple RGB interpolation
    const lowRgb = hexToRgb(lowHex);
    const highRgb = hexToRgb(highHex);

    for (let i = 0; i < 256; ++i) {
      const t = i / 255;
      const r = Math.round(lowRgb.r + (highRgb.r - lowRgb.r) * t);
      const g = Math.round(lowRgb.g + (highRgb.g - lowRgb.g) * t);
      const b = Math.round(lowRgb.b + (highRgb.b - lowRgb.b) * t);
      lut[i] = ((255 << 24) | (b << 16) | (g << 8) | r) >>> 0;
    }
    return lut;
  }

  // Spectrum mode: HSL interpolation with stable direction
  const loHsl = hexToHsl(lowHex);
  const hiHsl = hexToHsl(highHex);

  let h0 = loHsl.h;
  let h1 = hiHsl.h;

  // Initialize or maintain direction
  if (directionRef.current === 0) {
    const diff = h1 - h0;
    const wrappedDiff = diff > 0 ? diff - 1 : diff + 1;
    directionRef.current = Math.abs(diff) < Math.abs(wrappedDiff) ? 1 : -1;
  }

  // Apply consistent direction
  if (directionRef.current === -1) {
    if (h1 > h0) {
      h0 += 1;
    } else {
      h1 += 1;
    }
  }

  for (let i = 0; i < 256; ++i) {
    const t = i / 255;
    let h = h0 + (h1 - h0) * t;
    while (h > 1) h -= 1;
    while (h < 0) h += 1;

    // Always use full saturation and medium lightness for vibrant colors
    const s = 1.0;
    const l = 0.5;

    const { r, g, b } = hslToRgb(h, s, l);
    lut[i] = ((255 << 24) | (b << 16) | (g << 8) | r) >>> 0;
  }
  return lut;
}

function hexToRgb(hex: string): { r: number; g: number; b: number } {
  const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
  return result
    ? {
        r: parseInt(result[1], 16),
        g: parseInt(result[2], 16),
        b: parseInt(result[3], 16),
      }
    : { r: 0, g: 0, b: 0 };
}

const initialColors = generateRandomColors();
const initialSpectrumMode = Math.random() < 0.25;

export function useColorManagement(speedRef: React.MutableRefObject<number>) {
  const [lavaLowColor, setLavaLowColor] = useState(initialColors.low);
  const [lavaHighColor, setLavaHighColor] = useState(initialColors.high);
  const [rainbowMode, setRainbowMode] = useState(false);
  const [spectrumMode, setSpectrumMode] = useState(initialSpectrumMode);

  const gradientDirectionRef = useRef<number>(0);
  const heatLutRef = useRef<Uint32Array>(
    buildStableHeatLut256(initialColors.low, initialColors.high, gradientDirectionRef, initialSpectrumMode)
  );
  const lavaLowColorRef = useRef(initialColors.low);
  const lavaHighColorRef = useRef(initialColors.high);

  // Update heat LUT when colors or mode change
  useEffect(() => {
    lavaLowColorRef.current = lavaLowColor;
    lavaHighColorRef.current = lavaHighColor;
    heatLutRef.current = buildStableHeatLut256(lavaLowColor, lavaHighColor, gradientDirectionRef, spectrumMode);
  }, [lavaLowColor, lavaHighColor, spectrumMode]);

  // Rainbow mode: drift hues over time
  const rainbowLowHueRef = useRef(0);
  const rainbowHighHueRef = useRef(0);
  const rainbowLowSpeedRef = useRef(1);
  const rainbowHighSpeedRef = useRef(1.3);
  const prevRainbow = useRef(false);

  useEffect(() => {
    if (rainbowMode && !prevRainbow.current) {
      // Start from current colors
      rainbowLowHueRef.current = hexToHue(lavaLowColor);
      rainbowHighHueRef.current = hexToHue(lavaHighColor);
      // Random drift speeds: 0.5x to 2x base rate, random direction
      const lowSpeed = 0.5 + Math.random() * 1.5;
      const highSpeed = 0.5 + Math.random() * 1.5;
      const lowDirection = Math.random() < 0.5 ? 1 : -1;
      const highDirection = Math.random() < 0.5 ? 1 : -1;
      rainbowLowSpeedRef.current = lowSpeed * lowDirection;
      rainbowHighSpeedRef.current = highSpeed * highDirection;
    }
    prevRainbow.current = rainbowMode;
  }, [rainbowMode, lavaLowColor, lavaHighColor]);

  useEffect(() => {
    if (!rainbowMode) return;
    let id: number,
      lastT = performance.now();
    const tick = (now: number) => {
      const dt = now - lastT;
      lastT = now;
      const s = speedRef.current,
        norm = dt / 16.67,
        base = 0.02;
      rainbowLowHueRef.current =
        (rainbowLowHueRef.current + base * s * norm * rainbowLowSpeedRef.current) % 360;
      rainbowHighHueRef.current =
        (rainbowHighHueRef.current + base * s * norm * rainbowHighSpeedRef.current) % 360;

      // If within 45 degrees, ensure they're moving apart as fast as possible
      const lowHue = rainbowLowHueRef.current;
      const highHue = rainbowHighHueRef.current;
      const diff = highHue - lowHue;
      const wrappedDiff = diff > 0 ? diff - 360 : diff + 360;

      // Use signed distance to determine which way is shorter
      const signedDistance = Math.abs(diff) < Math.abs(wrappedDiff) ? diff : wrappedDiff;
      const angularDistance = Math.abs(signedDistance);

      if (angularDistance < 45) {
        const lowSpeed = rainbowLowSpeedRef.current;
        const highSpeed = rainbowHighSpeedRef.current;

        // Determine correct directions to maximize separation
        const lowShouldBeNegative = signedDistance > 0;
        const highShouldBePositive = signedDistance > 0;

        // Fix directions if they're wrong, with new random speeds
        if (lowShouldBeNegative && lowSpeed > 0) {
          const newSpeed = 0.5 + Math.random() * 1.5;
          rainbowLowSpeedRef.current = -newSpeed;
        } else if (!lowShouldBeNegative && lowSpeed < 0) {
          const newSpeed = 0.5 + Math.random() * 1.5;
          rainbowLowSpeedRef.current = newSpeed;
        }

        if (highShouldBePositive && highSpeed < 0) {
          const newSpeed = 0.5 + Math.random() * 1.5;
          rainbowHighSpeedRef.current = newSpeed;
        } else if (!highShouldBePositive && highSpeed > 0) {
          const newSpeed = 0.5 + Math.random() * 1.5;
          rainbowHighSpeedRef.current = -newSpeed;
        }
      }

      setLavaLowColor(hslToHex(rainbowLowHueRef.current, 1, 0.5));
      setLavaHighColor(hslToHex(rainbowHighHueRef.current, 1, 0.5));
      id = requestAnimationFrame(tick);
    };
    id = requestAnimationFrame(tick);
    return () => cancelAnimationFrame(id);
  }, [rainbowMode, speedRef]);

  return {
    lavaLowColor,
    setLavaLowColor,
    lavaHighColor,
    setLavaHighColor,
    rainbowMode,
    setRainbowMode,
    spectrumMode,
    setSpectrumMode,
    heatLutRef,
  };
}
